.usluga-block {
  .wrapper {
    border-radius: 8px;
    padding:       40px;
    background:    var(--seryy);
  }

  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  .subtitle {
    font-family: var(--second-family);
    font-weight: 700;
    @include r-font(24, 20);
    line-height: 120%;
    color:       var(--chernyy);
    margin-bottom: 24px;
  }

  .list {
    display: flex;
    flex-direction: column;
    gap: 24px;
    counter-reset: steps;
    margin-bottom: 35px;
    li {
      position: relative;
      display: flex;
      align-items: center;
      gap: 8px;

      &:before {
        counter-increment: steps;
        content: counter(steps) '.';
        font-family: var(--second-family);
        font-weight: 400;
        @include r-font(32, 24);
        text-transform: uppercase;
        color: var(--bezhevyy)
      }
    }
  }

  .text-block {
    border: 1px solid var(--siniy);
    border-radius: 8px;
    padding: 14px 20px;
    margin-bottom: clamp(40px, 3vw, 60px);
  }
}